<template>
<div class="login_color">
  <div class="login_box">


<!--    登录表单-->
<!--    用户名-->
  <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px"
           class="login_Form">
    <h2>登录界面</h2>
    <el-form-item prop="username">
      <el-input  v-model="loginForm.username"
                placeholder="请选输入用户名"
                prefix-icon="el-icon-user">
      </el-input>
    </el-form-item>

<!--登录密码-->
      <el-form-item prop="password">
        <el-input  type="password"
                  v-model="loginForm.password"
                  placeholder="请选输入密码"
                  prefix-icon="el-icon-lock">
        </el-input>
        </el-form-item>

<!--按钮区域-->
      <el-form-item class="btns">
        <el-button type="success" @click="login" round>登录</el-button>
        <el-button type="info" @click="restLoginForm" round>重置</el-button>
      </el-form-item>
    </el-form>

  </div>
</div>
</template>

<script>
  export default {
    data() {
      return {
        //表单数据绑定对象
        loginForm: {
          username: 'admin',
          password: '123456',
        },
        //验证输入框对象
        loginFormRules: {
          username: [
            {required: true, message: "请输入登录名称", trigger: "blur"},
            {min: 3, max: 10, message: "长度在3到10个字符之间", trigger: "blur"}
          ],
          password: [
            {required: true, message: "请输入登录密码", trigger: "blur"},
            {min: 6, max: 15, message: "长度在6到15个字符之间", trigger: "blur"}
          ]
        }
      }
    },
    methods: {
      restLoginForm() {
        // console.log(this)
        this.$refs.loginFormRef.resetFields();
      },
      login() {
        this.$refs.loginFormRef.validate( valid => {
          if (valid)
              {this.$message.success("登陆成功"),
                this.$router.push('/home')}
                // console.log('登录成功');}
              else
              {
                this.$message.error("登陆失败！")
              }
        })

        // if (this.loginForm.username = 'admin',
        //      this.loginForm.password = 123456)
        // {this.$message.success("登陆成功")
        //   console.log('登录成功');}
        // else
        // {
        //   this.$message.error("登陆失败！")
        // }

        }
      }


  }
</script>

<style scoped>

.login_color{
  background: #2b4b6b;
  height: 100%;
}

.login_box{
  width: 450px;
  height: 300px;
  background: wheat;
  border-radius: 5px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}

.login_Form{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding:0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}

</style>

