    <!--    <span>这是一段信息</span>-->
    <!--    <span slot="footer" class="dialog-footer">-->
    <!--      <el-button type="primary" @click="addDialogVisible = false">确认</el-button>-->
    <!--    <el-button @click="addDialogVisible = false">取消</el-button>-->
    <!--  </span>-->
    <!--  </el-dialog>-->


